.recipe-card{
    
    border: 1px solid black;
    width: auto;
    margin: 10px 0px 10px 20px;
    padding: 5px 5px 25px 5px;
    box-shadow: 1px 1px 2.5px black;
    color: white;
    font-size: larger;
    
}

.recipe-card:hover{

    transform: scale(1.02,1.02);

}

.recipe-container{

    text-align: center;

}

.btn{

    background-color: rgb(250, 150, 0);
    border: none;
    color: white;
    padding: 3px 3px;
    text-align: center;
    font-size: medium;
    font-weight: bolder;
    text-decoration: none;
    cursor: pointer;
    margin-bottom: 5px;

}

.btn:hover{
    transform: scale(1.1,1.1);
    box-shadow: 1px 1px black ;
}
